import React, { useState, useEffect } from "react";
import { withRouter } from "react-router";
import MetaTags from "react-meta-tags";
import { Row, Col, Card, CardBody, Table } from "reactstrap";

import moment from "moment";

// datatable related plugins
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory, {
  PaginationProvider,
  PaginationListStandalone,
  SizePerPageDropdownStandalone,
} from "react-bootstrap-table2-paginator";

import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";
import "./dashboard.scss";

import { fetchHelper } from "../../helpers/apiHelper";
import { dashboardURL } from "../../helpers/urlHelpers";
import { Link } from "react-router-dom";

function UserDetails(props) {
  const { pathname } = props.location.state;
  const [user, setUser] = useState({ ...props.location.state.user });
  const [details, setDetails] = useState([]);
  const [sizePerPage, setSizePerPage] = useState(10);

  useEffect(async () => {
    await fetchUserDetails(user._id);
  }, []);

  const fetchUserDetails = async (user) => {
    try {
      const options = {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ user }),
      };

      let data = await fetchHelper(dashboardURL + "/user/details", options);
      if (data.status) {
        setDetails(data.data.history);
      } else {
        alert(data.message);
      }
    } catch (err) {
      console.error(err);
      alert(err.message);
    }
  };

  const formatDate = (date) => moment(date).format("DD MMM, YYYY");

  const columns = [
    {
      dataField: "startDate",
      text: "Start Date",
      sort: true,
      formatter: (cellContent, row) => formatDate(cellContent),
      footer: (data) => `Total : ${data.length}`,
    },
    {
      dataField: "endDate",
      text: "End Date",
      sort: true,
      formatter: (cellContent, row) => formatDate(cellContent),
      footer: (data) => "",
    },
    {
      dataField: "planDuration",
      text: "Duration",
      sort: true,
      footer: (data) => "",
    },
    {
      dataField: "amount",
      text: "Amount",
      sort: true,
      footer: (data) => data.reduce((prev, curr) => prev + curr, 0),
    },
  ];

  const defaultSorted = [
    {
      dataField: "endDate",
      order: "desc",
    },
  ];

  // Custom Pagination Toggle
  const sizePerPageList = [
    { text: "5", value: 5 },
    { text: "10", value: 10 },
    { text: "15", value: 15 },
    { text: "All", value: details.length },
  ];

  const pageOptions = {
    sizePerPage,
    totalSize: details.length,
    sizePerPageList,
    custom: true,
  };

  const { SearchBar } = Search;

  return (
    <React.Fragment>
      <div className="page-content">
        <MetaTags>
          <title>Dashboard | User</title>
        </MetaTags>
        <div className="container-fluid">
          <Breadcrumbs title="Dashboard" breadcrumbItem={user.username} />
          <Row>
            <Col className="col-12">
              <Card>
                <CardBody>
                  {/* Pagination Provider */}
                  <PaginationProvider
                    pagination={paginationFactory(pageOptions)}
                    keyField="_id"
                    columns={columns}
                    data={details}
                  >
                    {({ paginationProps, paginationTableProps }) => (
                      <ToolkitProvider
                        keyField="_id"
                        columns={columns}
                        data={details}
                        search
                      >
                        {(toolkitProps) => (
                          <React.Fragment>
                            {/* Search Bar & Add Party Button */}
                            <Row className="mb-2">
                              <Col sm="6" xs="6">
                                <div className="search-box mb-2 d-inline-block">
                                  <div className="position-relative">
                                    <SearchBar {...toolkitProps.searchProps} />
                                    <i className="bx bx-search-alt search-icon" />
                                  </div>
                                </div>
                              </Col>
                              {
                                <Col sm="6" xs="6">
                                  <div>
                                    <Link
                                      to={{ pathname }}
                                      className="btn btn-info btn-md float-end"
                                    >
                                      Back
                                    </Link>
                                  </div>
                                </Col>
                              }
                            </Row>

                            {/* User Details Row */}
                            <div className="table-responsive">
                              <h5>User Details</h5>
                              <Table className="table align-center party-details-custom-table table-striped">
                                <tbody>
                                  <tr>
                                    <th>Firm Name</th>
                                    <td>{user.firmName}</td>
                                    <th></th>
                                    <td></td>
                                    <th></th>
                                    <td></td>
                                  </tr>
                                  <tr>
                                    <th>Mobile No.</th>
                                    <td>{user.mobileNo}</td>
                                    <th>GST No.</th>
                                    <td>{user.gstNo}</td>
                                    <th>PAN No.</th>
                                    <td>{user.panNo}</td>
                                  </tr>
                                  <tr>
                                    <th>Address</th>
                                    <td>{user.address}</td>
                                    <th>State</th>
                                    <td>{user.state}</td>
                                    <th></th>
                                    <td></td>
                                  </tr>
                                  <tr>
                                    <th>Status</th>
                                    <td>
                                      {user.status ? "Active" : "Blocked"}
                                    </td>
                                    <th hidden={user.status}>Block Reason</th>
                                    <td hidden={user.status}>{user.reason}</td>
                                    <th></th>
                                    <td></td>
                                    <th hidden={!user.status}></th>
                                    <td hidden={!user.status}></td>
                                  </tr>
                                </tbody>
                              </Table>
                            </div>

                            {/* Table Row */}
                            <Row className="mt-2">
                              <Col xl="12">
                                <div className="table-responsive">
                                  <h5>User History</h5>
                                  <BootstrapTable
                                    keyField={"_id"}
                                    responsive
                                    bordered={false}
                                    striped={false}
                                    defaultSorted={defaultSorted}
                                    classes={"table align-middle table-nowrap"}
                                    headerWrapperClasses={"thead-light"}
                                    {...toolkitProps.baseProps}
                                    {...paginationTableProps}
                                  />
                                </div>
                              </Col>
                            </Row>

                            {/* Pagination Row, i.e., Size per page option & Page number option */}
                            <Row className="align-items-md-center mt-30">
                              <Col className="inner-custom-pagination d-flex">
                                <div className="d-inline">
                                  <SizePerPageDropdownStandalone
                                    {...paginationProps}
                                  />
                                </div>
                                <div className="text-md-right ms-auto">
                                  <PaginationListStandalone
                                    {...paginationProps}
                                  />
                                </div>
                              </Col>
                            </Row>
                          </React.Fragment>
                        )}
                      </ToolkitProvider>
                    )}
                  </PaginationProvider>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </div>
      </div>
    </React.Fragment>
  );
}

export default withRouter(UserDetails);
