import React, { useState, useEffect } from "react";
import { withRouter } from "react-router";
import MetaTags from "react-meta-tags";
import {
  Row,
  Col,
  Card,
  CardBody,
  CardTitle,
  Button,
  Modal,
  ModalBody,
  Input,
  Form,
  ModalHeader,
} from "reactstrap";

import { Edit, Trash2, BookOpen } from "react-feather";

// datatable related plugins
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory, {
  PaginationProvider,
  PaginationListStandalone,
  SizePerPageDropdownStandalone,
} from "react-bootstrap-table2-paginator";

import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";
import "./dashboard.scss";

import { fetchHelper } from "../../helpers/apiHelper";
import { dashboardURL, partyURL, userURL } from "../../helpers/urlHelpers";
import { CityList } from "../../common/CityList";
import { StateList } from "../../common/StateList";
import { Link } from "react-router-dom";

function Dashboard(props) {
  const [users, setUsers] = useState([]);
  const [user, setUser] = useState({});
  const [sizePerPage, setSizePerPage] = useState(10);
  const [addUserModal, setAddUserModal] = useState(false);
  const [blockModalOpen, setBlockModalOpen] = useState(false);
  const [activateModalOpen, setActivateModalOpen] = useState(false);
  const [form, setForm] = useState({});

  useEffect(async () => {
    await fetchUsers();
  }, []);

  const fetchUsers = async () => {
    try {
      let data = await fetchHelper(dashboardURL + "/user", { method: "POST" });
      if (data.status) {
        setUsers(data.data);
      } else {
        alert(data.message);
      }
    } catch (err) {
      console.error(err);
      alert(err.message);
    }
  };

  const handleInputChange = (evt) =>
    setForm({ ...form, [evt.target.name]: evt.target.value });

  const handleBlockSubmit = async (evt) => {
    evt.preventDefault();

    let body = JSON.stringify({ reason: form.reason, user: user._id });

    let headers = {
      "Content-Type": "application/json",
    };
    let options = {
      method: "POST",
      headers,
      body,
    };

    let data = await fetchHelper(dashboardURL + "/user/deactivate", options);
    alert(data.message);
    if (data.status) {
      setBlockModalOpen(false);
      setForm({});
      setUser({});
      await fetchUsers();
    } else {
      setBlockModalOpen(true);
    }
  };

  const handleActivateSubmit = async (evt) => {
    evt.preventDefault();

    if (!form.planDuration || form.planDuration == "0") {
      return alert("Select Plan Duration");
    }

    let body = JSON.stringify({
      planDuration: form.planDuration,
      user: user._id,
      amount: form.amount,
    });

    let headers = {
      "Content-Type": "application/json",
    };
    let options = {
      method: "POST",
      headers,
      body,
    };

    let data = await fetchHelper(dashboardURL + "/user/activate", options);
    alert(data.message);
    if (data.status) {
      setActivateModalOpen(false);
      setForm({});
      setUser({});
      await fetchUsers();
    } else {
      setActivateModalOpen(true);
    }
  };

  const handleUserCreate = async (evt) => {
    evt.preventDefault();

    if (form.password != form.confirmPassword) {
      return alert("Enter matching passwords");
    }

    if (!form.userType || form.userType == "0")
      return alert("Select User Type");

    delete form.confirmPassword;

    let body = JSON.stringify({ ...form });

    let headers = {
      "Content-Type": "application/json",
    };
    let options = {
      method: "POST",
      headers,
      body,
    };

    let data = await fetchHelper(userURL + "/create", options);
    alert(data.message);
    if (data.status) {
      setAddUserModal(false);
      setForm({});
      await fetchUsers();
    } else {
      setAddUserModal(true);
    }
  };

  const columns = [
    {
      dataField: "username",
      text: "User Name",
      sort: true,
    },
    {
      dataField: "firmName",
      text: "Firm Name",
      sort: true,
    },
    {
      dataField: "mobileNo",
      text: "Mobile No.",
      sort: true,
    },
    {
      dataField: "gstNo",
      text: "GST No.",
      sort: true,
    },
    {
      dataField: "panNo",
      text: "PAN No.",
      sort: true,
    },
    {
      dataField: "address",
      text: "Address",
      sort: false,
    },
    {
      dataField: "state",
      text: "State",
      sort: true,
    },
    {
      dataField: "status",
      text: "Active ?",
      sort: false,
      formatter: (cellContent, row) => {
        return (
          <div className="text-center">
            <input
              type="checkbox"
              className="form-check-input"
              id={row._id}
              checked={row.status}
              onChange={(evt) => {
                setUser(row);
                if (row.status) setBlockModalOpen(true);
                else setActivateModalOpen(true);
              }}
            />
          </div>
        );
      },
    },
    {
      dataField: "reason",
      text: "Reason",
      sort: true,
    },
    {
      dataField: "action",
      isDummyField: true,
      text: "Action",
      formatter: (cellContent, row) => (
        <div className="d-flex" style={{ placeContent: "center" }}>
          <Link
            to={{
              pathname: "/user",
              state: { user: row, pathname: "/dashboard" },
            }}
          >
            <BookOpen size={20} className="text-success" />
          </Link>
        </div>
      ),
    },
  ];

  const defaultSorted = [
    {
      dataField: "username",
      order: "asc",
    },
  ];

  // Custom Pagination Toggle
  const sizePerPageList = [
    { text: "5", value: 5 },
    { text: "10", value: 10 },
    { text: "15", value: 15 },
    { text: "20", value: 20 },
    { text: "25", value: 25 },
    { text: "All", value: users.length },
  ];

  const pageOptions = {
    sizePerPage,
    totalSize: users.length,
    sizePerPageList,
    custom: true,
  };

  const { SearchBar } = Search;

  return (
    <React.Fragment>
      <div className="page-content">
        <MetaTags>
          <title>Dashboard | User</title>
        </MetaTags>
        <div className="container-fluid">
          <Breadcrumbs title="Dashboard" breadcrumbItem="user" />
          <Row>
            <Col className="col-12">
              <Card>
                <CardBody>
                  {/* Pagination Provider */}
                  <PaginationProvider
                    pagination={paginationFactory(pageOptions)}
                    keyField="_id"
                    columns={columns}
                    data={users}
                  >
                    {({ paginationProps, paginationTableProps }) => (
                      <ToolkitProvider
                        keyField="_id"
                        columns={columns}
                        data={users}
                        search
                      >
                        {(toolkitProps) => (
                          <React.Fragment>
                            {/* Search Bar & Add Party Button */}
                            <Row className="mb-2">
                              <Col sm="6" xs="6">
                                <div className="search-box mb-2 d-inline-block">
                                  <div className="position-relative">
                                    <SearchBar {...toolkitProps.searchProps} />
                                    <i className="bx bx-search-alt search-icon" />
                                  </div>
                                </div>
                              </Col>
                              <Col sm="6" xs="6">
                                <div>
                                  <Button
                                    className="btn btn-success btn-md float-end"
                                    onClick={() => setAddUserModal(true)}
                                  >
                                    Add User
                                  </Button>
                                </div>
                              </Col>
                            </Row>

                            {/* Table Row */}
                            <Row>
                              <Col xl="12">
                                <div className="table-responsive">
                                  <BootstrapTable
                                    keyField={"_id"}
                                    responsive
                                    bordered={false}
                                    striped={false}
                                    defaultSorted={defaultSorted}
                                    classes={"table align-middle table-nowrap"}
                                    headerWrapperClasses={"thead-light"}
                                    {...toolkitProps.baseProps}
                                    {...paginationTableProps}
                                  />
                                </div>
                              </Col>
                            </Row>

                            {/* Pagination Row, i.e., Size per page option & Page number option */}
                            <Row className="align-items-md-center mt-30">
                              <Col className="inner-custom-pagination d-flex">
                                <div className="d-inline">
                                  <SizePerPageDropdownStandalone
                                    {...paginationProps}
                                  />
                                </div>
                                <div className="text-md-right ms-auto">
                                  <PaginationListStandalone
                                    {...paginationProps}
                                  />
                                </div>
                              </Col>
                            </Row>
                          </React.Fragment>
                        )}
                      </ToolkitProvider>
                    )}
                  </PaginationProvider>

                  {/* Modal For Create User */}
                  <Row>
                    <Modal size="lg" isOpen={addUserModal}>
                      <ModalBody>
                        <ModalHeader
                          toggle={() => setAddUserModal(!addUserModal)}
                          tag="h4"
                          className="mb-0"
                        >
                          {"Add User"}
                        </ModalHeader>
                        <Card>
                          <CardBody>
                            <Form
                              onSubmit={handleUserCreate}
                              autoComplete="off"
                            >
                              <Row>
                                <Col md="6" className="mb-2">
                                  <label htmlFor="username">
                                    Username{" "}
                                    <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="text"
                                    placeholder="User Name"
                                    name="username"
                                    required
                                    value={form.username}
                                    onChange={handleInputChange}
                                  />
                                </Col>
                                <Col md="6" className="mb-2">
                                  <label htmlFor="firmName">
                                    Firm Name{" "}
                                    <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="text"
                                    placeholder="Firm Name"
                                    name="firmName"
                                    required
                                    value={form.firmName}
                                    onChange={handleInputChange}
                                  />
                                </Col>
                                <Col md="6" className="mb-2">
                                  <label htmlFor="password">
                                    Password{" "}
                                    <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="password"
                                    placeholder="Password"
                                    name="password"
                                    required
                                    value={form.password}
                                    onChange={handleInputChange}
                                  />
                                </Col>
                                <Col md="6" className="mb-2">
                                  <label htmlFor="confirmPassword">
                                    Confirm Password{" "}
                                    <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="password"
                                    placeholder="Confirm Password"
                                    name="confirmPassword"
                                    required
                                    value={form.confirmPassword}
                                    onChange={handleInputChange}
                                  />
                                </Col>
                              </Row>
                              <Row>
                                <Col md="4" className="mb-2">
                                  <label htmlFor="mobileNo">
                                    Mobile No.{" "}
                                    <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="tel"
                                    placeholder="XXXXXXXXXX"
                                    name="mobileNo"
                                    pattern="[0-9]{10}"
                                    required
                                    value={form.mobileNo}
                                    onChange={handleInputChange}
                                  />
                                </Col>
                                <Col md="4" className="mb-2">
                                  <label htmlFor="gstNo">
                                    GST No.{" "}
                                    <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="text"
                                    placeholder="GST No."
                                    name="gstNo"
                                    required
                                    value={form.gstNo}
                                    onChange={handleInputChange}
                                  />
                                </Col>
                                <Col md="4" className="mb-2">
                                  <label htmlFor="panNo">
                                    PAN No.{" "}
                                    <span style={{ color: "red" }}>*</span>
                                  </label>
                                  <Input
                                    type="text"
                                    placeholder="PAN No."
                                    name="panNo"
                                    required
                                    value={form.panNo}
                                    onChange={handleInputChange}
                                  />
                                </Col>
                              </Row>
                              <Row>
                                <Col md="12" className="mb-2">
                                  <label htmlFor="address">Address</label>
                                  <Input
                                    type="text"
                                    placeholder="Address"
                                    name="address"
                                    value={form.address}
                                    onChange={handleInputChange}
                                  />
                                </Col>
                              </Row>
                              <Row>
                                <Col md="6" className="mb-2">
                                  <label htmlFor="state">State</label>
                                  <Input
                                    type="select"
                                    name="state"
                                    value={form.state}
                                    onChange={handleInputChange}
                                  >
                                    <option value="0">Select State</option>
                                    {StateList.map(({ state }, index) => (
                                      <option value={state} key={index}>
                                        {state}
                                      </option>
                                    ))}
                                  </Input>
                                </Col>
                                <Col md="6" className="mb-2">
                                  <label htmlFor="userType">User Type</label>
                                  <Input
                                    type="select"
                                    name="userType"
                                    value={form.userType}
                                    onChange={handleInputChange}
                                  >
                                    <option value="0">Select User Type</option>
                                    <option value="user">User</option>
                                    <option value="admin">Admin</option>
                                  </Input>
                                </Col>
                              </Row>
                              <Row className="mt-2 mb-0">
                                <Col
                                  md="12"
                                  className="d-flex gap-3 halign-center"
                                >
                                  <Button
                                    type="button"
                                    className="btn btn-info btn-md mr-1"
                                    onClick={() => {
                                      setForm({});
                                      setAddUserModal(false);
                                    }}
                                  >
                                    Cancel
                                  </Button>
                                  <Button
                                    type="submit"
                                    className="btn btn-success btn-md ml-1"
                                  >
                                    Save
                                  </Button>
                                </Col>
                              </Row>
                            </Form>
                          </CardBody>
                        </Card>
                      </ModalBody>
                    </Modal>
                  </Row>

                  {/* Modal For Activate User */}
                  <Row>
                    <Modal size="md" isOpen={activateModalOpen}>
                      <ModalBody>
                        <ModalHeader
                          toggle={() =>
                            setActivateModalOpen(!activateModalOpen)
                          }
                          tag="h4"
                          className="mb-0"
                        >
                          {"Activate User"}
                        </ModalHeader>
                        <Card>
                          <CardBody>
                            <Form
                              onSubmit={handleActivateSubmit}
                              autoComplete="off"
                            >
                              <Row>
                                <Col md="12" className="mb-2">
                                  <label htmlFor="planDuration">
                                    Plan Duration
                                  </label>
                                  <Input
                                    type="select"
                                    name="planDuration"
                                    placeholder="Select Plan"
                                    required
                                    onChange={handleInputChange}
                                  >
                                    <option value="0">Select Plan</option>
                                    <option value="3 Months">3 Months</option>
                                    <option value="6 Months">6 Months</option>
                                    <option value="1 Year">1 Year</option>
                                    <option value="2 Years">2 Years</option>
                                  </Input>
                                </Col>
                                <Col md="12" className="mb-2">
                                  <label htmlFor="partyName">
                                    Enter Amount
                                  </label>
                                  <Input
                                    type="number"
                                    step="any"
                                    min={0}
                                    placeholder="Amount"
                                    name="amount"
                                    required
                                    onChange={handleInputChange}
                                  />
                                </Col>
                              </Row>
                              <Row className="mt-2 mb-0 text-center">
                                <Col
                                  md="12"
                                  className="d-flex gap-3 halign-center"
                                >
                                  <Button
                                    type="button"
                                    className="btn btn-info btn-md mr-1"
                                    onClick={() => {
                                      setForm({});
                                      setUser({});
                                      setActivateModalOpen(false);
                                    }}
                                  >
                                    Cancel
                                  </Button>
                                  <Button
                                    type="submit"
                                    className="btn btn-success btn-md ml-1"
                                  >
                                    Activate
                                  </Button>
                                </Col>
                              </Row>
                            </Form>
                          </CardBody>
                        </Card>
                      </ModalBody>
                    </Modal>
                  </Row>

                  {/* Modal For Block User */}
                  <Row>
                    <Modal size="md" isOpen={blockModalOpen}>
                      <ModalBody>
                        <ModalHeader
                          toggle={() => {
                            setBlockModalOpen(!blockModalOpen);
                          }}
                          tag="h4"
                          className="mb-0"
                        >
                          {"Block User"}
                        </ModalHeader>
                        <Card>
                          <CardBody>
                            <Form
                              onSubmit={handleBlockSubmit}
                              autoComplete="off"
                            >
                              <Row>
                                <Col md="12" className="mb-2">
                                  <label htmlFor="partyName">
                                    Enter Reason To Block :{" "}
                                    <b>{user.username}</b>
                                  </label>
                                  <Input
                                    type="text"
                                    placeholder="Reason to block user"
                                    name="reason"
                                    required
                                    onChange={handleInputChange}
                                  />
                                </Col>
                              </Row>

                              <Row className="mt-2 mb-0 text-center">
                                <Col
                                  md="12"
                                  className="d-flex gap-3 halign-center"
                                >
                                  <Button
                                    type="button"
                                    className="btn btn-info btn-md mr-1"
                                    onClick={() => {
                                      setForm({});
                                      setUser({});
                                      setBlockModalOpen(false);
                                    }}
                                  >
                                    Cancel
                                  </Button>
                                  <Button
                                    type="submit"
                                    className="btn btn-danger btn-md ml-1"
                                  >
                                    Block
                                  </Button>
                                </Col>
                              </Row>
                            </Form>
                          </CardBody>
                        </Card>
                      </ModalBody>
                    </Modal>
                  </Row>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </div>
      </div>
    </React.Fragment>
  );
}

export default withRouter(Dashboard);
